import axios from 'axios'

//export const BASEURL = axios.defaults.baseURL = 'https://api.bizzan.pro/';

//export const BASEURL = axios.defaults.baseURL = 'http://139.180.196.45:3000/';


// 在请求拦截器中设置请求头

const instance = axios.create({
    timeout: 5000
});

// 在请求拦截器中设置请求头
instance.interceptors.request.use(config => {
    // 获取浏览器当前的语言设置
    const language = navigator.language || navigator.userLanguage;

    // 设置Accept-Language头部
    config.headers['Accept-Language'] = language;


    const token = localStorage.getItem('Token');

    if (token) {
        config.headers['Authorization'] = `Bearer ${token}`;
    }


    return config;
}, error => {
    return Promise.reject(error);
});

export const get = (url, params = {}) => {
    return new Promise((resolve, reject) => {
        instance.get(url, {
            params: params
        })
            .then(response => {
                resolve(response.data);

            })
            .catch(err => {
                reject(err);
            })
    })
}

export const getConfig = (url, params = {}, config = {}) => {
    return new Promise((resolve, reject) => {
        instance.get(url, {
            headers: config
        })
            .then(response => {
                resolve(response.data);
            })
            .catch(err => {
                reject(err);
            })
    })
}

export const postConfig = (url, data = {}, config) => {
    return new Promise((resolve, reject) => {
        instance.post(url, data, config)
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}



export const post = (url, data = {}) => {
    console.log('url',url)
    return new Promise((resolve, reject) => {
        instance.post(url, data, { withCredentials: true })
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}
