import { get, getConfig, post, postConfig } from './http.js'

//使用者登入帳號
export const login = (data) => post('/api/auth/login', data)
//使用者登入驗證
export const loginVerify = (data) => post('/api/auth/loginVerify', data)

//登出會員帳號
export const logout = (data) => post('/api/auth/logout', data)

//建立會員帳號
export const createUserAccount = (data) => post('/api/auth/register', data)
//註冊認證
export const checkRegisterVerify = (data) => post('/api/auth/registerVerify', data)

//取得使用者訊息
export const getMember = (data, config) => post('/api/auth/me', data)

//取得EMAIL認證碼
export const sendCheckEmail = (data) => post('/api/auth/email', data)
//傳送EMAIL認證
export const sendEmailVerify = (data) => post('/api/auth/emailVerify', data)
//取得EMAIL認證碼
export const sendCheckMobile = (data) => post('/api/auth/mobile', data)
//傳送EMAIL認證
export const sendMobileVerify = (data) => post('/api/auth/mobileVerify', data)
//重設會員密碼
export const resetMemberPassword = (data) => post('/api/member/resetPassword', data)
//編輯會員帳號資訊
export const editMember = (data) => post('/api/member/edit', data)

/*----------幣安交易所API-------------*/
//測試功能連線
export const BNping = (el) => get(el + '/api/v3/ping')

export const BNexchangeInfo = (el) => get('https://api.binance.com/api/v3/exchangeInfo')
